<template>
  <div class="body-bg">
    <div class="nav-bar">
      <div class="nav-bar-left">
        <i class="iconfont iconguanbi" />
      </div>
    </div>
    <div class="login-h t4">重置密码</div>
    <div class="login-h-mini">还没有账号，立即<a>注册</a></div>

    <div class="login-form">
      <van-cell-group>
        <div class="input-item">
          <van-field
            ref="name"
            clearable
            is-highlight
            is-title-latent
            placeholder="请输入手机号"
            title="手机号"
          />
        </div>
        <div class="input-item">
          <van-field
            ref="id"
            clearable
            is-highlight
            is-title-latent
            placeholder="请输入验证码"
            title="验证码"
          >
            <div class="input-operator mini">
              <van-button v-show="!computedTime" plain round size="mini" type="info" @click.prevent="getVerifyCode">
                获取验证码
              </van-button>
              <van-button v-show="computedTime" plain round size="mini" type="info" @click.prevent>
                已发送({{ computedTime }}s)
              </van-button>
            </div>
          </van-field>
        </div>
        <div class="input-item password">
          <van-field
            ref="id"
            clearable
            is-highlight
            is-title-latent
            placeholder="请输入新密码"
            title="新密码"
            :type="eyesChange ==true?'text':'password'"
          />

          <!-- 显示与隐藏密码 -->
          <div v-if="eyesChange ==true" class="input-operator showpassword" @click="showEye()">
            <i class="iconfont iconkejian" />
          </div>
          <div v-else class="input-operator showpassword" @click="showEye()">
            <i class="iconfont iconbukejian" />
          </div>

        </div>
      </van-cell-group>

      <!-- 按钮 begin -->
      <div class="login-button">
        <van-button round size="large" type="info" @click="showSucceedToast">完 成</van-button>
      </div>
      <!-- 按钮 end -->
    </div>
  </div>
</template>

<script>
import { pageRouter } from '@/common/js/mixin.js'

export default {
  name: 'GysZhmm',
  components: {

  },
  mixins: [pageRouter],
  data() {
    return {
      checked: 0, // 选择供应商来源
      eyesChange: false, // 是否展示密码
      profiletitle: '注册',
      computedTime: 0 // 倒数记时
    }
  },

  methods: {
    showSucceedToast() {
      this.$toast.success('密码重置成功')
    },
    /* 控制是否展示密码*/
    showEye() {
      this.eyesChange = !this.eyesChange
    },
    async getVerifyCode() {
      // if (this.rightPhoneNumber) {
      this.computedTime = 30
      this.timer = setInterval(() => {
        this.computedTime--
        if (this.computedTime === 0) {
          clearInterval(this.timer)
        }
      }, 1000)
      // } else {
      //   this.showAlert = true;
      //   this.alertText = '请检查手机号';
      //   return
      // }
    }
  }
}

</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none;}
</style>
